.font-tarsus{
    font-family: 'Tarsus';
    color: #4d4d4d;
}
.menu{
    margin-left: 20%;
}

.nav-link:hover{
    -webkit-animation-name: link-color-change;
            animation-name: link-color-change;
    -webkit-animation-duration: 500ms;
            animation-duration: 500ms;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
    cursor: -webkit-grab;
    cursor: grab;
}

.active-link{
    color:#61D0D4 !important;
    -webkit-animation: none !important;
            animation: none !important;
}


@-webkit-keyframes  link-color-change{
    from {color:#4d4d4d}
    to {color: #61d0d4}
}


@keyframes  link-color-change{
    from {color:#4d4d4d}
    to {color: #61d0d4}
}


